<template>
  <v-parallax dark src="../../public/images/main_5.jpg" class="par">
    <v-container>
      <router-link :to="{ name: 'home' }">
        <img
          id="dog"
          src="../../public/images/logo.svg"
          height="150"
          width="150"
        />
      </router-link>
      <h2>用户信息</h2>

      <v-card class="mx-auto" max-width="500">
        <v-card-text>
          <p class="display-1 text--primary">
            {{ username }}
          </p>
          <p>学号：{{ userid }}</p>
          <div class="text--primary">
            邮箱：{{ email }}<br />
            <div v-show = "is_rent">
            在租借{{ location }}<br />
            还剩余：{{ days }}
            </div>
          </div>
        </v-card-text>
      </v-card>
      <div v-show="display_list">
        <h2>借租历史</h2>
        <v-card class="mx-auto" max-width="500" style="overflow: visible">
          <v-card-text>
            <div class="text--primary" v-for="(item, i) in list">
              <h4>租借人：{{ item.user_name }}</h4>
              租借开始：{{ item.rent_start }}<br />
              租借结束：{{ item.rent_exp }}<br />
              联系方式：{{ item.phone_number }}<br />
              用途：{{ item.reason }}
              <v-divider></v-divider>
            </div>
          </v-card-text>
        </v-card>
      </div>
      
      <h2>评分 <v-rating v-model="rating"></v-rating></h2>
      <h2>留言</h2>

      <div v-show="provider_display">
        <router-link :to="{ name: 'my_equip' }">
          <v-btn v-on:click="rent_manage">目录</v-btn>
        </router-link>
        <router-link :to="{ name: 'rent_equip' }" v-show="manager_display">
          <v-btn v-on:click="rent_manage">目录</v-btn>
        </router-link>
      </div>
    </v-container>
  </v-parallax>
</template>
<script>
import axios from "axios";
import Post_View from "./Post_View.vue";

import VueHotelDatepicker from "@northwalker/vue-hotel-datepicker";

let path;
export default {
  name: "Post_View",
  components: {
    Post_View,
    VueHotelDatepicker,
  },

  data() {
    return {
      date: ["2020/09/25", "2020/09/26"],
      rating: 4,
      is_staff: 0,
      username: "",
      rent_equip_id:'',
      days:'',
      email:'',
      userid:'',
      equip_status: "",
      phone_number: "",
      location: "",
      description: "",
      form: {
        reason: "",
        phone_number: "",
        start: "",
        end: "",
        equipid: 0,
        user_id: "",
        user_name: "",
      },
      set: {
        equipid: "",
        aaa: "",
      },
      history_name: "",
      rent_start: "",
      rent_exp: "",
      reason: "",
      list: [],
      display_list: false,
      display_waiting_list: false,
      waiting_list: [],
      rent_status: 0,
      is_rent: false,
    };
  },
  created() {
    this.my_display = false;
    path = window.location.href;
    path = path.split("#");
    console.log(path);
    path[1] = path[1].slice(12);
    console.log("what is postid", path[1]);
    //this.fetch();
  },
  methods: {
      wait_accept(item){
          let wrap ={}
          let info= {}
          wrap['equipid'] = path[1]
          wrap['id'] = item.id
          
          info["userid"] = localStorage.getItem("userid");
        info["is_rent"] = this.is_rent;
        info["rent_status"] = this.rent_status;
          
          
          axios.post('api/equip/wait_allow/',wrap)
          .then((r)=>{
              console.log(r)
              this.is_rent = true
              info["is_rent"] = this.is_rent
               axios.post("api/update_user_status/", info).then((r) => {
              console.log(r);
              console.log('motherfucker')
              location.reload()
            });
            
            

          }).catch((e)=>console.log(e))
      },
      wait_deny(item){
          
                    let info= {}

          info["userid"] = localStorage.getItem("userid");
        info["is_rent"] = this.is_rent;
        info["rent_status"] = this.rent_status;
          let wrap ={}
          wrap['equipid'] = path[1]
          wrap['id'] = item.id
          axios.put('api/equip/wait_allow/',wrap)
          .then((r)=>{
              console.log(r)
              
              this.rent_status = 0
              this.is_rent = false
              info["is_rent"] = this.is_rent
              info["rent_status"] = this.rent_status;
               axios.post("api/update_user_status/", info).then((r) => {
              console.log(r);
              console.log('motherfucker')
              location.reload()
              
               })
              
          }).catch((e)=>console.log(e))
      },
      
      
      
    data(value) {
      this.form.start = value.start;
      this.form.end = value.end;
      console.log(value);
    },
    send() {
      // sub['reason'] = this.form.description
      //sub['phone_number'] = this.form.phone_number
      this.form.user_id = localStorage.getItem("userid");
      this.form.equipid = path[1];
      this.form.user_name = localStorage.getItem("username");
      console.log(this.form);
      axios
        .put(`api/equip/waitlist/`, this.form)
        .then((r) => {
          console.log(r);
          location.reload();
        })
        .catch((e) => console.log(e));
    },
  },
  mounted: function () {
    axios
      .post("api/send_user_status/", localStorage.getItem("userid"))
      .then((r) => {
        console.log(r.data);
        this.is_staff = r.data.is_staff;
        this.is_rent = r.data.is_rent;
        this.rent_status = r.data.rent_status;
        if (this.is_staff == 1) {
          this.manager_display = true;
        } else if (this.is_staff == 2) {
          this.provider_display = true;
        } else {
          this.user_display = true;
          console.log(this.user_display);
        }
        axios.post('api/render',localStorage.getItem("userid"))
        .then((r)=>{
            console.log(r)
            this.username = r.data.info.username
            this.userid = r.data.info.userid
            this.email = r.data.info.email
            this.rent_equip_id = r.data.info.apply_equip_id
            this.days = this.r.data.days
            this.is_rent = this.r.data.is_rent
            
            console.log('aaaa',r.data.info.username)
        })
       
      })
      .catch(
        (e) => {
          alert("wrong user");
        }
        // alert3ing();
      ); //에러뜨면 알람
  },
};
</script>

<style>
* {
  text-align: center;
}
.par {
  height: 2000px !important;
}
.banner-area {
  background: url(../../public/images/banner-bg.jpg) center;
  background-size: cover;
  width: 100%;
  height: 400px;
  line-height: 100px;
  margin: 0 auto;
}
#submit {
  background-color: gray;
  border-radius: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 80px;
  height: 30x;
}
input {
  margin: 5px 0px 10px 5px;
  width: 200px;
  border-radius: 8px;
  border: 1px solid burlywood;
}

</style>
